import EventsService from '@/services/EventsService';
import store from '@/store/store';

const state = {
  events: []
};

const getters = {
  events: state => state.events,
};
  
const actions = {

  async fetchEvents({commit}){
    const response = await EventsService.events();
    await commit('setEvents', response.data);
  },

  async deleteEvent({commit}, id){
    const response = await EventsService.delete_event(id);
    if (response.data.error === 0) {
      await store.dispatch('setErrorNo', 0);
      await store.dispatch('setMsg', response.data.msg);
      await commit("removeEvent", id);
    } else {
      await store.dispatch('setErrorNo', 1);
      await store.dispatch('setMsg', response.data.msg);
    }
  },

  async createEvent({commit}, event){
    const response = await EventsService.new_event(event);
    
    if (response.data.error === 0) {
      await store.dispatch('setErrorNo', 0);
      await store.dispatch('setMsg', response.data.msg);
      await commit("addEvent", response.data.event);
    } else {
      await store.dispatch('setErrorNo', 1);
      await store.dispatch('setMsg', response.data.msg);
    }
  },

  async updateEvent({commit}, Event){
    const response = await EventsService.update_event(Event.id, Event.event);
    
    if (response.data.error === 0) {
      await store.dispatch('setErrorNo', 0);
      await store.dispatch('setMsg', response.data.msg);
      await commit("updateEvent", {id: Event.id, event: response.data.event});
    } else {
      await store.dispatch('setErrorNo', 1);
      await store.dispatch('setMsg', response.data.msg);
    }
  }
  
};

const mutations = {
    setEvents: (state, events) => {
      state.events = events
    },
    removeEvent: (state, id) => {
      state.events = state.events.filter(event => event.id !== id);
    },
    addEvent: (state, event) => {
      state.events.unshift(event);
    },
    updateEvent: (state, Event) => {
      
      for (let index = 0; index < state.events.length; index++) {
        const event = state.events[index];
        if (event.id === Event.id) {
          state.events[index] = Event.event;
          break;
        }
        
      }
    }
};
  
export default {
  state,
  getters,
  mutations,
  actions 
}