// import Vue from 'vue'
import { createRouter, createWebHistory  } from 'vue-router'
import Home from '../views/Home.vue'

import store from "../store/store.js";
// import { sync } from 'vuex-router-sync';

// Vue.use(VueRouter)

// const unsync = sync(store, router);

// unsync();

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  },
  {
    path: '/our-programmes',
    name: 'Our Programmes',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/OurProgrammes.vue')
  },
  {
    path: '/units',
    name: 'Units',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/Units.vue')
  },
  {
    path: '/contact-us',
    name: 'Contact Us',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/Contact.vue')
  },
  {
    path: '/console/login',
    name: 'Admin Login',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import('../components/ConsoleLogin.vue'),
    meta: { guest: true },
  },

  {
    path: '/console',
    name: 'Admin Index',
    component: () => import('../components/ConsoleIndex.vue'),
    meta: {requiresAuth: true},
  },

  {
    path: '/console/profile',
    name: 'Admin Profile',
    component: () => import('../components/ConsoleProfile.vue'),
    meta: {requiresAuth: true},
  },

  {
    path: '/console/security',
    name: 'Admin Securiy',
    component: () => import('../components/ConsoleSecurity.vue'),
    meta: {requiresAuth: true},
  },

  {
    path: '/console/users',
    name: 'Admin Users',
    component: () => import('../components/ConsoleUsers.vue'),
    meta: {requiresAuth: true},
  },

  {
    path: '/console/programmes',
    name: 'Admin Programmes',
    component: () => import('../components/ConsoleProgrammes.vue'),
    meta: {requiresAuth: true},
  },

  {
    path: '/console/events',
    name: 'Admin Events',
    component: () => import('../components/ConsoleEvents.vue'),
    meta: {requiresAuth: true},
  },

  {
    path: '/console/offices',
    name: 'Admin Offices',
    component: () => import('../components/ConsoleOffices.vue'),
    meta: {requiresAuth: true},
  },

  {
    path: '/console/executives',
    name: 'Admin Executives',
    component: () => import('../components/ConsoleExecutives.vue'),
    meta: {requiresAuth: true},
  },

  {
    path: '/console/units',
    name: 'Admin Units',
    component: () => import('../components/ConsoleUnits.vue'),
    meta: {requiresAuth: true},
  },

  {
    path: '/console/settings',
    name: 'Admin Settings',
    component: () => import('../components/ConsoleSettings.vue'),
    meta: {requiresAuth: true},
  },

  { path: "*", component: () => import('../views/404.vue') }
]

const router = createRouter({
  history: createWebHistory(),
  routes
});

router.beforeEach((to, from, next) => {
  if(to.matched.some(record => record.meta.requiresAuth)) {
    if (store.getters.isAuthenticated) {
      next()
      return
    }
    next('/console/login')
  } else {
    next()
  }
})

router.beforeEach((to, from, next) => {
  if (to.matched.some((record) => record.meta.guest)) {
    if (store.getters.isAuthenticated) {
      next("/console");
      return;
    }
    next();
  } else {
    next();
  }
});

export default router;
