import Api from '@/services/Api';

export default {
    generation (id) {
        return Api().get(`generation/${id}`);
    },

    generations () {
        return Api().get(`generations`);
    },

    new_generation (credentials) {
        return Api().post(`generation`, credentials);
    },

    update_generation (id, credentials) {
        return Api().put(`generation/${id}`, credentials);
    },

    delete_generation (id) {
        return Api().delete(`generation/${id}`);
    },

}