import ExecutivesService from '@/services/ExecutivesService';
import GenerationsService from '@/services/GenerationsService';
import store from '@/store/store';

const state = {
  executives: [],
  generations: [],
  gen_executives: [],
};

const getters = {
  executives: state => state.executives,
  gen_executives: state => state.gen_executives,
  generations: state => state.generations,
};
  
const actions = {

  async fetchGenerations({commit}){
    const response = await GenerationsService.generations();
    await commit('setGenerations', response.data);
  },

  async fetchExecutives({commit}){
    const response = await ExecutivesService.executives();
    await commit('setExecutives', response.data);
  },

  async fetchGenerationExecutives({commit}, gen_id){
    const response = await ExecutivesService.generation_executives(gen_id);
    await commit('setGenerationExecutives', response.data);
  },

  async deleteGeneration({commit}, id){
    const response = await GenerationsService.delete_generation(id);
    if (response.data.error === 0) {
      await store.dispatch('setErrorNo', 0);
      await store.dispatch('setMsg', response.data.msg);
      await commit("removeGeneration", id);
    } else {
      await store.dispatch('setErrorNo', 1);
      await store.dispatch('setMsg', response.data.msg);
    }
  },

  async deleteExecutive({commit}, id){
    const response = await ExecutivesService.delete_executive(id);
    if (response.data.error === 0) {
      await store.dispatch('setErrorNo', 0);
      await store.dispatch('setMsg', response.data.msg);
      await commit("removeExecutive", id);
    } else {
      await store.dispatch('setErrorNo', 1);
      await store.dispatch('setMsg', response.data.msg);
    }
  },

  async createGeneration({commit}, generation){
    const response = await GenerationsService.new_generation(generation);
    
    if (response.data.error === 0) {
      await store.dispatch('setErrorNo', 0);
      await store.dispatch('setMsg', response.data.msg);
      await commit("addGeneration", response.data.generation);
    } else {
      await store.dispatch('setErrorNo', 1);
      await store.dispatch('setMsg', response.data.msg);
    }
  },

  async createExecutive({commit}, executive){
    const response = await ExecutivesService.new_executive(executive);
    
    if (response.data.error === 0) {
      await store.dispatch('setErrorNo', 0);
      await store.dispatch('setMsg', response.data.msg);
      await commit("addExecutive", response.data.executive);
    } else {
      await store.dispatch('setErrorNo', 1);
      await store.dispatch('setMsg', response.data.msg);
    }
  },

  async updateGeneration({commit}, Generation){
    const response = await GenerationsService.update_generation(Generation.id, Generation.generation);
    
    if (response.data.error === 0) {
      await store.dispatch('setErrorNo', 0);
      await store.dispatch('setMsg', response.data.msg);
      await commit("updateGeneration", {id: Generation.id, generation: response.data.generation});
    } else {
      await store.dispatch('setErrorNo', 1);
      await store.dispatch('setMsg', response.data.msg);
    }
  },

  async updateExecutive({commit}, Executive){
    const response = await ExecutivesService.update_executive(Executive.id, Executive.executive);
    
    if (response.data.error === 0) {
      await store.dispatch('setErrorNo', 0);
      await store.dispatch('setMsg', response.data.msg);
      await commit("updateExecutive", {id: Executive.id, executive: response.data.executive});
    } else {
      await store.dispatch('setErrorNo', 1);
      await store.dispatch('setMsg', response.data.msg);
    }
  }
  
};

const mutations = {
    setGenerations: (state, generations) => {
      state.generations = generations
    },
    setExecutives: (state, executives) => {
      state.executives = executives
    },
    setGenerationExecutives: (state, executives) => {
      state.gen_executives = executives
    },
    removeGeneration: (state, id) => {
      state.generations = state.generations.filter(generation => generation.id !== id);
    },
    removeExecutive: (state, id) => {
      state.gen_executives = state.gen_executives.filter(gen_executive => gen_executive.id !== id);
    },
    addGeneration: (state, generation) => {
      state.generations.unshift(generation);
    },
    addExecutive: (state, executive) => {
      state.gen_executives.unshift(executive);
    },
    updateGeneration: (state, Generation) => {
      
      for (let index = 0; index < state.generations.length; index++) {
        const generation = state.generations[index];
        if (generation.id === Generation.id) {
          state.generations[index] = Generation.generation;
          break;
        }
        
      }
    },
    updateExecutive: (state, Executive) => {
      
      for (let index = 0; index < state.gen_executives.length; index++) {
        const gen_executive = state.gen_executives[index];
        if (gen_executive.id === Executive.id) {
          state.gen_executives[index] = Executive.gen_executive;
          break;
        }
        
      }
    }
};
  
export default {
  state,
  getters,
  mutations,
  actions 
}