import UnitsService from '@/services/UnitsService';
import store from '@/store/store';

const state = {
  units: [],
  unit_details: null,
  unit_info_details: [],
};

const getters = {
  units: state => state.units,
  unit_details: state => state.unit_details,
  unit_info_details: state => state.unit_info_details
  
};
  
const actions = {

  async fetchUnits({commit}){
    const response = await UnitsService.units();
    await commit('setUnits', response.data);
  },

  async fetchUnitDetails({commit}, id){
    await commit('setUnitDetails', id);
  },

  async fetchUnitInfoDetails({commit}, ID){
    const response = await UnitsService.units_details(ID);
    await commit('setUnitInfoDetails', response.data);
  },

  async fetchUnitInfoDetailsByGenID({commit}, id){
    const response = await UnitsService.units_details_by_gen_id(id);
    await commit('setUnitInfoDetails', response.data);
  },

  async fetchUnitInfoDetailsByUnitID({commit}, id){
    const response = await UnitsService.units_details_by_unit_id(id);
    await commit('setUnitInfoDetails', response.data);
  },

  async deleteUnit({commit}, id){
    const response = await UnitsService.delete_unit(id);
    if (response.data.error === 0) {
      await store.dispatch('setErrorNo', 0);
      await store.dispatch('setMsg', response.data.msg);
      await commit("removeUnit", id);
    } else {
      await store.dispatch('setErrorNo', 1);
      await store.dispatch('setMsg', response.data.msg);
    }
  },

  async deleteUnitInfoDetail({commit}, id){
    const response = await UnitsService.delete_unit_detail(id);
    if (response.data.error === 0) {
      await store.dispatch('setErrorNo', 0);
      await store.dispatch('setMsg', response.data.msg);
      await commit("removeUnitInfoDetail", id);
    } else {
      await store.dispatch('setErrorNo', 1);
      await store.dispatch('setMsg', response.data.msg);
    }
  },

  async createUnit({commit}, unit){
    const response = await UnitsService.new_unit(unit);
    
    if (response.data.error === 0) {
      await store.dispatch('setErrorNo', 0);
      await store.dispatch('setMsg', response.data.msg);
      await commit("addUnit", response.data.unit);
    } else {
      await store.dispatch('setErrorNo', 1);
      await store.dispatch('setMsg', response.data.msg);
    }
  },

  async createUnitInfoDetail({commit}, unit_detail){
    const response = await UnitsService.new_unit_detail(unit_detail);
    
    if (response.data.error === 0) {
      await store.dispatch('setErrorNo', 0);
      await store.dispatch('setMsg', response.data.msg);
      await commit("addUnitInfoDetail", response.data.unit_detail);
    } else {
      await store.dispatch('setErrorNo', 1);
      await store.dispatch('setMsg', response.data.msg);
    }
  },

  async updateUnit({commit}, Unit){
    const response = await UnitsService.update_unit(Unit.id, Unit.unit);
    
    if (response.data.error === 0) {
      await store.dispatch('setErrorNo', 0);
      await store.dispatch('setMsg', response.data.msg);
      await commit("updateUnit", {id: Unit.id, unit: response.data.unit});
    } else {
      await store.dispatch('setErrorNo', 1);
      await store.dispatch('setMsg', response.data.msg);
    }
  },

  async updateUnitDetail({commit}, Unit_detail){
    const response = await UnitsService.update_unit_detail(Unit_detail.id, Unit_detail.unit_detail);
    
    if (response.data.error === 0) {
      await store.dispatch('setErrorNo', 0);
      await store.dispatch('setMsg', response.data.msg);
      await commit("updateUnitInfoDetail", {id: Unit_detail.id, unit_detail: response.data.unit_detail});
    } else {
      await store.dispatch('setErrorNo', 1);
      await store.dispatch('setMsg', response.data.msg);
    }
  }
  
};

const mutations = {
    setUnits: (state, units) => {
      state.units = units
    },
    setUnitDetails: (state, id) => {
      const unit_details = state.units.filter(unit => unit.id === id);
      if (unit_details.length > 0) {
        console.log(unit_details[0]);
        state.unit_details = unit_details[0];
      }
      else{
        state.unit_details = null;
      }
    },
    setUnitInfoDetails: (state, unit_info_details) => {
      state.unit_info_details = unit_info_details;
    },

    removeUnit: (state, id) => {
      state.units = state.units.filter(unit => unit.id !== id);
    },
    removeUnitInfoDetail: (state, id) => {
      state.unit_info_details = state.unit_info_details.filter(unit_detail => unit_detail.id !== id);
    },
    addUnit: (state, unit) => {
      state.units.unshift(unit);
    },
    addUnitInfoDetail: (state, unit_detail) => {
      state.unit_info_details.unshift(unit_detail);
    },
    updateUnit: (state, Unit) => {
      for (let index = 0; index < state.units.length; index++) {
        const unit = state.units[index];
        if (unit.id === Unit.id) {
          state.units[index] = Unit.unit;
          break;
        }
      }
    },
    updateUnitInfoDetail: (state, Unit_detail) => {
      
      for (let index = 0; index < state.unit_info_details.length; index++) {
        const unit_detail = state.unit_info_details[index];
        if (unit_detail.id === Unit_detail.id) {
          state.unit_info_details[index] = Unit_detail.unit_detail;
          break;
        }
        
      }
    }
};
  
export default {
  state,
  getters,
  mutations,
  actions 
}