import OfficesService from '@/services/OfficesService';
import store from '@/store/store';

const state = {
  offices: [],
};

const getters = {
  offices: state => state.offices,
};
  
const actions = {

  async fetchOffices({commit}){
    const response = await OfficesService.offices();
    await commit('setOffices', response.data);
  },

  async deleteOffice({commit}, id){
    const response = await OfficesService.delete_office(id);
    if (response.data.error === 0) {
      await store.dispatch('setErrorNo', 0);
      await store.dispatch('setMsg', response.data.msg);
      await commit("removeOffice", id);
    } else {
      await store.dispatch('setErrorNo', 1);
      await store.dispatch('setMsg', response.data.msg);
    }
  },

  async createOffice({commit}, office){
    const response = await OfficesService.new_office(office);
    
    if (response.data.error === 0) {
      await store.dispatch('setErrorNo', 0);
      await store.dispatch('setMsg', response.data.msg);
      await commit("addOffice", response.data.user);
    } else {
      await store.dispatch('setErrorNo', 1);
      await store.dispatch('setMsg', response.data.msg);
    }
  },

  async updateOffice({commit}, Office){
    const response = await OfficesService.update_office(Office.id, Office.office);
    if (response.data.error === 0) {
      await store.dispatch('setErrorNo', 0);
      await store.dispatch('setMsg', response.data.msg);
      await commit("updateOffice", {id: Office.id, office: response.data.office});
    } else {
      await store.dispatch('setErrorNo', 1);
      await store.dispatch('setMsg', response.data.msg);
    }
  },
  
};

const mutations = {
    setOffices: (state, offices) => {
      state.offices = offices
    },
    removeOffice: (state, id) => {
      state.offices = state.offices.filter(office => office.id !== id);
    },
    updateOffice: (state, Office) => {
      for (let index = 0; index < state.offices.length; index++) {
        const office = state.offices[index];
        if (office.id === Office.office.id) {
          state.offices[index] = Office.office;
          break;
        }
        
      }
      
    },
    addOffice: (state, office) => {
      state.offices.unshift(office);
    }
};
  
export default {
  state,
  getters,
  mutations,
  actions 
}