import ContentsService from '@/services/ContentsService';
import store from '@/store/store';

const state = {
  contents: [],
  content_details: []
};

const getters = {
  contents: state => state.contents,
  content_details: state => state.content_details
};
  
const actions = {

  async fetchContents({commit}){
    const response = await ContentsService.contents();
    await commit('setContents', response.data);
  },

  async fetchContentDetails({commit}, id){
    const response = await ContentsService.content_details(id);
    await commit('setContentDetails', response.data);
  },

  async deleteContent({commit}, id){
    const response = await ContentsService.delete_content(id);
    if (response.data.error === 0) {
      await store.dispatch('setErrorNo', 0);
      await store.dispatch('setMsg', response.data.msg);
      await commit("removeContent", id);
    } else {
      await store.dispatch('setErrorNo', 1);
      await store.dispatch('setMsg', response.data.msg);
    }
  },

  async deleteContentDetail({commit}, id){
    const response = await ContentsService.delete_content_detail(id);
    if (response.data.error === 0) {
      await store.dispatch('setErrorNo', 0);
      await store.dispatch('setMsg', response.data.msg);
      await commit("removeContentDetail", id);
    } else {
      await store.dispatch('setErrorNo', 1);
      await store.dispatch('setMsg', response.data.msg);
    }
  },

  async createContent({commit}, content){
    const response = await ContentsService.new_content(content);
    
    if (response.data.error === 0) {
      await store.dispatch('setErrorNo', 0);
      await store.dispatch('setMsg', response.data.msg);
      await commit("addContent", response.data.content);
    } else {
      await store.dispatch('setErrorNo', 1);
      await store.dispatch('setMsg', response.data.msg);
    }
  },

  async createContentDetail({commit}, content_detail){
    const response = await ContentsService.new_content_detail(content_detail);
    
    if (response.data.error === 0) {
      await store.dispatch('setErrorNo', 0);
      await store.dispatch('setMsg', response.data.msg);
      await commit("addContentDetail", response.data.content_detail);
    } else {
      await store.dispatch('setErrorNo', 1);
      await store.dispatch('setMsg', response.data.msg);
    }
  },

  async updateContent({commit}, Content){
    const response = await ContentsService.update_content(Content.id, Content.content);
    
    if (response.data.error === 0) {
      await store.dispatch('setErrorNo', 0);
      await store.dispatch('setMsg', response.data.msg);
      await commit("updateContent", {id: Content.id, content: response.data.content});
    } else {
      await store.dispatch('setErrorNo', 1);
      await store.dispatch('setMsg', response.data.msg);
    }
  },

  async updateContentDetail({commit}, Content_detail){
    const response = await ContentsService.update_content_detail(Content_detail.id, Content_detail.content_detail);
    
    if (response.data.error === 0) {
      await store.dispatch('setErrorNo', 0);
      await store.dispatch('setMsg', response.data.msg);
      await commit("updateContentDetail", {id: Content_detail.id, content_detail: response.data.content_detail});
    } else {
      await store.dispatch('setErrorNo', 1);
      await store.dispatch('setMsg', response.data.msg);
    }
  }
  
};

const mutations = {
  setContents: (state, contents) => {
    state.contents = contents
  },
  setContentDetails: (state, content_details) => {
    state.content_details = content_details
  },
  removeContent: (state, id) => {
    state.contents = state.contents.filter(content => content.id !== id);
  },
  removeContentDetail: (state, id) => {
    state.content_details = state.content_details.filter(content_detail => content_detail.id !== id);
  },
  addContent: (state, content) => {
    state.contents.unshift(content);
  },
  addContentDetail: (state, content_detail) => {
    state.content_details.unshift(content_detail);
  },
  updateContent: (state, Content) => {
    
    for (let index = 0; index < state.contents.length; index++) {
      const content = state.contents[index];
      if (content.id === Content.id) {
        state.contents[index] = Content.content;
        break;
      }
      
    }
  },
  updateContentDetail: (state, Content_detail) => {
    
    for (let index = 0; index < state.content_details.length; index++) {
      const content_detail = state.content_details[index];
      if (content_detail.id === Content_detail.id) {
        state.content_details[index] = Content_detail.content_detail;
        break;
      }
      
    }
  }
};
  
export default {
  state,
  getters,
  mutations,
  actions 
}