import Api from '@/services/Api';

export default {
    content (id) {
        return Api().get(`content/${id}`);
    },

    content_detail (id) {
        return Api().get(`content_detail/${id}`);
    },

    contents () {
        return Api().get(`contents`);
    },

    content_details (id) {
        return Api().get(`content_details/${id}`);
    },

    new_content (credentials) {
        return Api().post(`content`, credentials);
    },

    new_content_detail (credentials) {
        return Api().post(`content_detail`, credentials);
    },

    update_content (id, credentials) {
        return Api().put(`content/${id}`, credentials);
    },

    update_content_detail (id, credentials) {
        return Api().put(`content_detail/${id}`, credentials);
    },

    delete_content (id) {
        return Api().delete(`content/${id}`);
    },

    delete_content_detail (id) {
        return Api().delete(`content_detail/${id}`);
    },

}