import Api from '@/services/Api';

export default {
    event (id) {
        return Api().get(`event/${id}`);
    },

    events () {
        return Api().get(`events`);
    },

    new_event (credentials) {
        return Api().post(`event`, credentials);
    },

    update_event (id, credentials) {
        return Api().put(`event/${id}`, credentials);
    },

    delete_event (id) {
        return Api().delete(`event/${id}`);
    },

}