import ProgrammesService from '@/services/ProgrammesService';
import store from '@/store/store';

const state = {
  programmes: []
};

const getters = {
  programmes: state => state.programmes,
};
  
const actions = {

  async fetchProgrammes({commit}){
    const response = await ProgrammesService.programmes();
    await commit('setProgrammes', response.data);
  },

  async deleteProgramme({commit}, id){
    const response = await ProgrammesService.delete_programme(id);
    if (response.data.error === 0) {
      await store.dispatch('setErrorNo', 0);
      await store.dispatch('setMsg', response.data.msg);
      await commit("removeProgramme", id);
    } else {
      await store.dispatch('setErrorNo', 1);
      await store.dispatch('setMsg', response.data.msg);
    }
  },

  async createProgramme({commit}, programme){
    const response = await ProgrammesService.new_programme(programme);
    
    if (response.data.error === 0) {
      await store.dispatch('setErrorNo', 0);
      await store.dispatch('setMsg', response.data.msg);
      await commit("addProgramme", response.data.programme);
    } else {
      await store.dispatch('setErrorNo', 1);
      await store.dispatch('setMsg', response.data.msg);
    }
  },

  async updateProgramme({commit}, Programme){
    const response = await ProgrammesService.update_programme(Programme.id, Programme.programme);
    
    if (response.data.error === 0) {
      await store.dispatch('setErrorNo', 0);
      await store.dispatch('setMsg', response.data.msg);
      await commit("updateProgramme", {id: Programme.id, programme: response.data.programme});
    } else {
      await store.dispatch('setErrorNo', 1);
      await store.dispatch('setMsg', response.data.msg);
    }
  }
  
};

const mutations = {
    setProgrammes: (state, programmes) => {
      state.programmes = programmes
    },
    removeProgramme: (state, id) => {
      state.programmes = state.programmes.filter(programme => programme.id !== id);
    },
    addProgramme: (state, programme) => {
      state.programmes.unshift(programme);
    },
    updateProgramme: (state, Programme) => {
      
      for (let index = 0; index < state.programmes.length; index++) {
        const programme = state.programmes[index];
        if (programme.id === Programme.id) {
          state.programmes[index] = Programme.programme;
          break;
        }
        
      }
    }
};
  
export default {
  state,
  getters,
  mutations,
  actions 
}