<script>

// import axios from 'axios';
import Header from '../components/layouts/Header';
import Footer from '../components/layouts/Footer';
import { mapActions, mapGetters } from 'vuex';

import VueSlickCarousel from 'vue-slick-carousel'
import 'vue-slick-carousel/dist/vue-slick-carousel.css'
// optional style for arrows & dots
import 'vue-slick-carousel/dist/vue-slick-carousel-theme.css'

export default {
  name: "app",
  components: {
    Header,
    Footer,
    VueSlickCarousel
  },
  data(){
    return{
      settings: {
        "dots": true,
        "infinite": false,
        "speed": 500,
        "slidesToShow": 2,
        "slidesToScroll": 2,
        "initialSlide": 0,
        "responsive": [
          {
            "breakpoint": 1024,
            "settings": {
              "slidesToShow": 2,
              "slidesToScroll": 2,
              "infinite": true,
              "dots": true
            }
          },
          {
            "breakpoint": 600,
            "settings": {
              "slidesToShow": 2,
              "slidesToScroll": 2,
              "initialSlide": 2
            }
          },
          {
            "breakpoint": 480,
            "settings": {
              "slidesToShow": 2,
              "slidesToScroll": 2
            }
          }
        ]
      },
      home_content: [],
      slide_contents: [],
      home_content_details: [],
      events: this.$store.getters.events.filter(event => event.visibility === 1)
    }
  },
  computed: mapGetters(['contents', 'content_details']),
  methods: {
    ...mapActions(['fetchContents', 'fetchContentDetails', 'fetchEvents'])
  },
  created(){
    this.fetchContents();
    this.fetchEvents();

    this.home_content = this.contents.filter(content => content.page === 'home');
    if (this.home_content.length > 0) {
      this.fetchContentDetails(this.home_content[0].id);
      this.home_content_details = this.content_details.filter(content_detail => content_detail.slide !== 1 && content_detail.content_id === this.home_content[0].id);
      this.slide_contents = this.content_details.filter(content_detail => content_detail.slide === 1 && content_detail.content_id === this.home_content[0].id);

      // console.log(this.slide_contents);
    }

  }
}

</script>

