import Api from '@/services/Api';

export default {
    executive (id) {
        return Api().get(`executive/${id}`);
    },

    executives () {
        return Api().get(`executives`);
    },

    generation_executives (gen_id) {
        return Api().get(`executives/${gen_id}`);
    },

    new_executive (credentials) {
        return Api().post(`executive`, credentials);
    },

    update_executive (id, credentials) {
        return Api().put(`executive/${id}`, credentials);
    },

    delete_executive (id) {
        return Api().delete(`executive/${id}`);
    },

}