import Api from '@/services/Api';

export default {
    programme (user_id) {
        return Api().get(`programme/${user_id}`);
    },

    programmes () {
        return Api().get(`programmes`);
    },

    new_programme (credentials) {
        return Api().post(`programme`, credentials);
    },

    update_programme (user_id, credentials) {
        return Api().put(`programme/${user_id}`, credentials);
    },

    delete_programme (user_id) {
        return Api().delete(`programme/${user_id}`);
    },

}