import Api from '@/services/Api';

export default {
    office (office_id) {
        return Api().get(`office/${office_id}`);
    },

    offices () {
        return Api().get(`offices`);
    },

    new_office (credentials) {
        return Api().post(`office`, credentials);
    },

    update_office (office_id, credentials) {
        return Api().put(`office/${office_id}`, credentials);
    },

    delete_office (office_id) {
        return Api().delete(`office/${office_id}`);
    },

}