import Api from '@/services/Api';

export default {
    unit (id) {
        return Api().get(`unit/${id}`);
    },

    unit_detail (id) {
        return Api().get(`unit_detail/${id}`);
    },

    units () {
        return Api().get(`units`);
    },

    units_details (ID) {
        return Api().get(`units_details/${ID.gen_id}/${ID.unit_id}`);
    },

    units_details_by_gen_id (id) {
        return Api().get(`unit_details/${id}`);
    },

    units_details_by_unit_id (id) {
        return Api().get(`unit_details/${id}`);
    },

    new_unit (credentials) {
        return Api().post(`unit`, credentials);
    },

    new_unit_detail (credentials) {
        return Api().post(`unit_detail`, credentials);
    },

    update_unit (id, credentials) {
        return Api().put(`unit/${id}`, credentials);
    },

    update_unit_detail (id, credentials) {
        return Api().put(`unit_detail/${id}`, credentials);
    },

    delete_unit (id) {
        return Api().delete(`unit/${id}`);
    },

    delete_unit_detail (id) {
        return Api().delete(`unit_detail/${id}`);
    },

}