import Vue from 'vue'
import Vuex from 'vuex'
import AuthenticationModule from './modules/AuthenticationModule';
import ProgrammesModule from './modules/ProgrammesModule';
import UsersModule from './modules/UsersModule';
import OfficesModule from './modules/OfficesModule';
import ExecutivesModule from './modules/ExecutivesModule';
import UnitsModule from './modules/UnitsModule';
import EventsModule from './modules/EventsModule';
import ContentsModule from './modules/ContentsModule';

import createPersistedState from "vuex-persistedstate";

// Load Vuex
Vue.use(Vuex)


// Create store
export default new Vuex.Store({
  modules: {
    AuthenticationModule,
    ProgrammesModule,
    UsersModule,
    OfficesModule,
    ExecutivesModule,
    UnitsModule,
    EventsModule,
    ContentsModule
  },
  plugins: [
    createPersistedState({
      AuthenticationModule,
      ProgrammesModule,
      UsersModule,
      OfficesModule,
      ExecutivesModule,
      UnitsModule,
      EventsModule,
      ContentsModule
    })
  ]
})
