import UsersService from '@/services/UsersService';
import store from '@/store/store';

const state = {
  users: [],
};

const getters = {
  users: state => state.users,
};
  
const actions = {

  async fetchUsers({commit}){
    const response = await UsersService.users();
    await commit('setUsers', response.data);
  },

  async deleteUser({commit}, id){
    const response = await UsersService.delete_user(id);
    if (response.data.error === 0) {
      await store.dispatch('setErrorNo', 0);
      await store.dispatch('setMsg', response.data.msg);
      await commit("removeUser", id);
    } else {
      await store.dispatch('setErrorNo', 1);
      await store.dispatch('setMsg', response.data.msg);
    }
  },

  async createUser({commit}, user){
    const response = await UsersService.new_user(user);
    
    if (response.data.error === 0) {
      await store.dispatch('setErrorNo', 0);
      await store.dispatch('setMsg', response.data.msg);
      await commit("addUser", response.data.user);
    } else {
      await store.dispatch('setErrorNo', 1);
      await store.dispatch('setMsg', response.data.msg);
    }
  }
  
};

const mutations = {
    setUsers: (state, users) => {
      state.users = users
    },
    removeUser: (state, id) => {
      state.users = state.users.filter(user => user.id !== id);
    },
    addUser: (state, user) => {
      state.users.push(user);
    }
};
  
export default {
  state,
  getters,
  mutations,
  actions 
}